import styled from "styled-components";

export const Brume = styled.div`
@keyframes changeBackground {
  0% {
    background: #031e30ea;
  }
  20% {
    background: #031e3086;
  }
  30% {
    background: #031e3000;
  }
  90% {
    background: #031e3000;
  }
  100% {
    background: #031e30ea;
  }
}
animation: changeBackground 20s linear infinite;
animation-direction: alternate;
position: absolute;
top: 0;
left: 0;
transition: 3s;
height: 50vh;
width: 100%;
z-index: 14;
filter: blur(4px);
`
const Nuage1 = styled.div`
position: absolute;
top: 30%;
left: 5%;
width: 40%;
height: 20px;
background-color: white;
border-radius: 50px 90px 60px 90px;
border-radius: 50%;
opacity: 10%;
z-index: 3;
animation: nuageMoove1 20s linear infinite;
@keyframes nuageMoove1 {
  from {
    transform: translateX(0px);

  }

  to {
    transform: translateX(100%);
  }
}
`