import styled from "styled-components";

export const RocheLeft_1_REFLECT = styled.div`
@keyframes upOmbre {
  from {
    height: 80px;
  }

  to {
    height: 30px;
  }
}

position: absolute;
top: 49.6vh;
left: 2.1vw;
width: 29.5%;
/* height: 60px; */
z-index: 22;
filter: blur(2px);

> div {
  animation: upOmbre 20s linear infinite;
  width: 100%;
  height: 60px;
  background-color: hsla(260, 36%, 40%, 0.562);
  opacity: 80%;
  clip-path: polygon(
    58% 43%,
    76% 53%,
    92% 56%,
    93% 77%,
    97% 100%,
    41% 100%,
    7% 100%,
    10% 72%,
    24% 60%,
    34% 48%
  );
  transform: rotateX(180deg) rotateY(0deg);
}
`

