import React from 'react'
import styled from 'styled-components'
import { Brume } from './styleComponents/brume'
import { Ciel } from './styleComponents/ciel'
import { Nuage2, Nuage3, Nuage4, Nuage5 } from './styleComponents/nuage'
import { RocheLeft_1_REFLECT, RocheLeft_2_REFLECT, RocheLeft_3_REFLECT } from './styleComponents/roche_reflect'
import { RocheLeft_1_TOP, RocheLeft_2_TOP, RocheLeft_3_TOP } from './styleComponents/roche'
import { Soleil , Soleil_reflect} from './styleComponents/soleil'
import { Water, Water2 } from './styleComponents/water'

const All = styled.div`
  position: absolute;
  background-color: #ddae4733;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
`



const FloDemo = () => {
  return (
    <>
      <div>
        {/* <All></All> */}
        <Ciel />
        <Brume />
        <Nuage2/>
        <Nuage3/>
        <Nuage4/>
        <Nuage5/>
        <Soleil>
          <div></div>
        </Soleil>
        <RocheLeft_1_TOP />
        <RocheLeft_2_TOP />
        <RocheLeft_3_TOP />

        {/* _________________________________________________________________*/}
        <Soleil_reflect>
          <div></div>
        </Soleil_reflect>
        <RocheLeft_1_REFLECT>
          <div></div>
        </RocheLeft_1_REFLECT>

        <RocheLeft_2_REFLECT>
          <div></div>
        </RocheLeft_2_REFLECT>

        <RocheLeft_3_REFLECT>
          <div></div>
        </RocheLeft_3_REFLECT>
        <Water />                       {/* z-index 21 */}
        <Water2 />
      </div>
    </>
  )
}

export default FloDemo
