import styled from "styled-components";

export const Nuage2 = styled.div`
position: absolute;
top: 25%;
left: -25%;
width: 40%;
height: 40px;
background-color: white;
border-radius: 50px 90px 60px 90px;
border-radius: 50%;
opacity: 10%;
z-index: 12;
animation: nuageMoove2 20s linear infinite;
@keyframes nuageMoove2 {
  from {
    transform: translateX(0px);

  }

  to {
    transform: translateX(150%);
  }
}
`

