import styled from "styled-components";

export const Soleil = styled.div`
@keyframes up {
  from {
    transform: translateY(0px);
    background: #ffffff;
  }

  to {
    transform: translateY(-600px);
    background: #ffffff;
  }
}

position: absolute;
width: 100%;
height: 100%;
z-index: 4;
> div {
  animation: up 20s linear infinite;
  height: 100px;
  width: 100px;
  border-radius: 100%;
  margin: 55vh auto;
  box-shadow: inset 0 0 1em white, 0 0 2em white;
}
`

export const Soleil_reflect = styled.div`
position: absolute;
width: 100%;
height: 100px;
z-index: 21;
top: 41vh;
>div{
  animation: down 20s linear infinite;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  background: #ffffff;
  box-shadow: inset 0 0 1em white, 0 0 2em white;

}
@keyframes down {
  0% {
    width: 10px;
    transform: translateY(0px) rotateX(90deg);
    opacity: 0%;
  }
  10% {
    opacity: 0%;
  }  
  100% {
    opacity: 60%;
    width: 200px;
    transform: translateY(300px) rotateX(45deg);
  }
}
`